<template>
    <v-app>
        <v-flex 
            d-flex
            justify-center 
            align-center 
            flex-column >
            <navbar
                @togglesettings="toggleSettings"/>
            <youtube
                style="border-radius: 4px; width: 700px; height: 427px"
                class="elevation-10"
                ref="youtube" 
                :player-vars="playerVars" 
                @playing="playing" 
                @paused="paused"
                @ready="ready" />
        </v-flex>

        <controls 
            :playerStuff="playerStuff" 
            :alerts="alerts"
            :currentPlaylist="currentPlaylist"
            @toggleplaylists="togglePlaylists" />

        <playlists 
            :show-playlists="showPlaylists" />

        <v-snackbar
            :style="[ $vuetify.breakpoint.smAndDown ? {'bottom': '0', 'left': '0', 'width': '100vw'} : {'bottom': '9rem', 'right': '2rem'} ]"
            v-model="alerts.shuffle"
            color="ThirdText--text primaryCard"
            multi-line
            bottom
            right
            :timeout="3000">Playlist Shuffled
            <v-btn @click="alerts.shuffle = false" color="primaryText" text large>close</v-btn>
        </v-snackbar>

        <v-snackbar
            :style="[ $vuetify.breakpoint.smAndDown ? {'bottom': '0', 'left': '0', 'width': '100vw'} : {'bottom': '9rem', 'right': '2rem'} ]"
            v-model="alerts.loop"
            color="ThirdText--text primaryCard"
            multi-line
            bottom
            right
            :timeout="3000">Playlist Looped
            <v-btn @click="alerts.loop = false" color="primaryText" text large>close</v-btn>
        </v-snackbar>
            
        <v-card :style="{'background-image': $vuetify.theme.themes[theme].background}" id="background"></v-card>
    </v-app>
</template>

<script>
import navbar from './components/navbar.vue';
import controls from './components/controls.vue';
import playlists from './components/playlists.vue';

export default {
    name: 'App',

    components: {
        navbar,
        controls,
        playlists
    },

    data () {
        return {
            playerVars: {
                list: 'PLvheczCxmsyQBg4Nkn_-T_IkKuEFq1bv9',
                listType: 'playlist',
                mute: 0,
                loop: 0,
                autoplay: 0,
                color: 'white',
                rel: 0,
                controls: 1,
                fs: 0,
                iv_load_policy: 3
            },
            playerStuff: {
                isPlaying: false,
                isLooped: false,
                isShuffled: false,
                volume: 25,
            },
            alerts: {
                shuffle: false,
                loop: false
            },
            currentPlaylist: {
                title: '80s Slow Cance Club',
                cover: '80s-slow-dance-club'
            },
            showPlaylists: false,
            activeTheme: false
        }
    },
    methods: {
        playing () {
            console.log('playing')
            this.playerStuff.isPlaying = true
        },
        paused () {
            console.log('paused')
            this.playerStuff.isPlaying = false
        },
        ready () {
            this.player.setVolume(this.volume)
            this.playerStuff.duration
        },
        toggleSettings () {
            this.activeTheme = !this.activeTheme
            this.$vuetify.theme.dark = this.activeTheme
        },
        togglePlaylists () {
            this.showPlaylists = !this.showPlaylists
        }
    },
    computed: {
        player () {
            return this.$refs.youtube.player
        },
        volume () {
            return this.playerStuff.volume
        },
        theme () {
            return (this.$vuetify.theme.dark) ? 'dark' : 'light'
        },

    },
    async mounted () {
        if (localStorage.volume) {
            this.playerStuff.volume = localStorage.volume
        }
    },
    watch: {
        volume (newVol) {
            localStorage.volume = newVol
            this.player.setVolume(newVol)
        }
    }
};
</script>

<style lang="scss">
html { overflow-y: auto !important }

.v-application--wrap {
    z-index: 1;
}

#background {
    width: 100%;
    height: 100%;
    position: fixed;
    filter: blur(10px);
    transform: scale(1.1);
    z-index: -1;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50%;
}
</style>