<template>
    <v-card width="100%" absolute bottom="0"  color="transparent">
        <v-flex class="d-flex align-center py-2">
            <v-list-item>
                <v-list-item-avatar 
                    tile 
                    size="75" 
                    class="elevation-10">
                    <v-img
                        aspect-ratio="1.33"
                        alt="thumbnail" 
                        :src="'images/playlists/' + currentPlaylist.cover + '.jpg'"
                        ></v-img>
                </v-list-item-avatar>

                <v-list-item-content>
                    <v-list-item-subtitle class="thirdText--text">Playlist</v-list-item-subtitle>
                    <v-list-item-title class="primaryText--text">{{ currentPlaylist.title }}</v-list-item-title>
                </v-list-item-content>
            </v-list-item>
                

            <v-spacer></v-spacer>
                
            <v-list-item class="justify-center">
                
                
                <v-tooltip top color="secondaryCard">
                    <template v-slot:activator="{ on }" >
                        <v-btn
                            :class="{ 'primaryText--text' : playerStuff.isLooped, 'thirdText--text': !playerStuff.isLooped}"
                            @click="toggleLoop()"
                            v-on="on"
                            icon
                            x-large>
                            <v-icon
                                v-if="playerStuff.isLooped == true"
                                size="24px"
                                v-text="'mdi-repeat'"></v-icon>
                            <v-icon
                                v-else
                                size="24px"
                                v-text="'mdi-repeat-off'"></v-icon>
                        </v-btn>
                    </template>
                    <span class="primaryText--text">Toggle Loop</span>
                </v-tooltip>

                <v-tooltip top color="secondaryCard">
                    <template v-slot:activator="{ on }" >
                        <v-btn
                            color="thirdText"
                            @click="prevSong()"
                            v-on="on"
                            icon
                            x-large>
                            <v-icon 
                                size="24px"
                                v-text="'mdi-skip-previous'"></v-icon>
                        </v-btn>
                    </template>
                    <span class="primaryText--text">Previous Song</span>
                </v-tooltip>
                
                <v-tooltip top color="secondaryCard">
                    <template v-slot:activator="{ on }" >
                        <v-btn
                            color="thirdText"
                            @click="playSong()"
                            v-if="playerStuff.isPlaying == false"
                            v-on="on"
                            icon
                            x-large>
                            <v-icon 
                                size="24px"
                                v-text="'mdi-play'"></v-icon>
                        </v-btn>
                        <v-btn
                            color="thirdText"
                            @click="pauseSong()"
                            v-else
                            v-on="on"
                            icon
                            x-large>
                            <v-icon 
                                size="24px"
                                v-text="'mdi-pause'"></v-icon>
                        </v-btn>
                    </template>
                    <span v-if="playerStuff.isPlaying == true" class="primaryText--text">Play Song</span>
                    <span v-else class="primaryText--text">Pause Song</span>
                </v-tooltip>
                
                <v-tooltip top color="secondaryCard">
                    <template v-slot:activator="{ on }" >
                        <v-btn
                            color="thirdText"
                            @click="nextSong()"
                            v-on="on"
                            icon
                            x-large>
                            <v-icon 
                                size="24px"
                                v-text="'mdi-skip-next'"></v-icon>
                        </v-btn>
                    </template>
                    <span class="primaryText--text">Next Song</span>
                </v-tooltip>
                
                <v-tooltip top color="secondaryCard">
                    <template v-slot:activator="{ on }" >
                        <v-btn
                            :class="{ 'primaryText--text' : playerStuff.isShuffled, 'thirdText--text': !playerStuff.isShuffled}"
                            @click="toggleShuffle()"
                            v-on="on"
                            icon
                            x-large>
                            <v-icon
                                v-if="playerStuff.isShuffled == true"
                                size="24px"
                                v-text="'mdi-shuffle'"></v-icon>
                            <v-icon 
                                v-else
                                size="24px"
                                v-text="'mdi-shuffle-disabled'"></v-icon>
                        </v-btn>
                    </template>
                    <span class="primaryText--text">Toggle Shuffle</span>
                </v-tooltip>
            </v-list-item>
            
            <v-spacer></v-spacer>
            
            <v-list-item class="justify-end">
                <v-slide-x-reverse-transition>
                    <v-card class="slideContainer" v-if="showSlider" color="transparent" flat>
                        <v-slider 
                            color="primaryText" 
                            track-color="secondaryText"
                            v-model="playerStuff.volume" 
                            thumb-label></v-slider>
                    </v-card>
                </v-slide-x-reverse-transition>
                
                <v-tooltip top color="secondaryCard" >
                    <template v-slot:activator="{ on }" >
                        <v-btn
                            color="thirdText"
                            @click="showSlider = !showSlider"
                            v-on="on"
                            icon
                            x-large>
                            <v-icon v-if="playerStuff.volume <= 0" size="24px" v-text="'mdi-volume-off'"></v-icon>
                            <v-icon v-if="playerStuff.volume > 0 && playerStuff.volume < 25" size="24px" v-text="'mdi-volume-low'"></v-icon>
                            <v-icon v-if="playerStuff.volume >= 25 && playerStuff.volume < 75" size="24px" v-text="'mdi-volume-medium'"></v-icon>
                            <v-icon v-if="playerStuff.volume >= 75" size="24px" v-text="'mdi-volume-high'"></v-icon>
                        </v-btn>
                    </template>
                    <span class="primaryText--text">Volume</span>
                </v-tooltip>
                
                
                <v-tooltip top color="secondaryCard">
                   <template v-slot:activator="{ on }" >
                       <v-btn
                            @click="togglePlaylists()"
                            color="thirdText"
                            v-on="on"
                            icon
                            x-large>
                            <v-icon 
                               size="24px"
                               v-text="'mdi-playlist-music'"></v-icon>
                       </v-btn>
                   </template>
                   <span class="primaryText--text">playlists</span>
                </v-tooltip>
            </v-list-item>
        </v-flex>
        <v-progress-linear
            :value="83"
            class="my-0"
            height="3"
            color="primaryText"></v-progress-linear>
    </v-card>
</template>

<script>

export default {
    name: 'controls',
    props: ['playerStuff', 'alerts', 'toggleplaylists', 'currentPlaylist'],
    data () {
        return {
            showSlider: false
        }
    },
    methods: {
        playSong () {
            this.player.playVideo()
        },
        pauseSong () {
            this.player.pauseVideo()
        },
        prevSong () {
            this.player.previousVideo()
        },
        async nextSong () {
            this.player.nextVideo()
        },
        toggleShuffle () {
            this.playerStuff.isShuffled = !this.playerStuff.isShuffled
            this.player.setShuffle(this.playerStuff.isShuffled)
            this.alerts.shuffle = this.playerStuff.isShuffled
        },
        toggleLoop () {
            this.playerStuff.isLooped = !this.playerStuff.isLooped
            this.player.setLoop(this.playerStuff.isLooped)
            this.alerts.loop = this.playerStuff.isLooped
        },
        togglePlaylists () {
            this.$emit('toggleplaylists')
        }
    },
    computed: {
        player () {
            return this.$root.$children[0].player
        }
    }
}
</script>

<style lang="scss" scoped>
.slideContainer {
    width: 150px;
    top: 10px;
}
</style>
